}
.nav-li{
  color: white;
  text-align: center;
  padding: 10px;
  font-size: 20px;
  font-weight: 800;
  border-bottom: 2px solid white;
}
.nav-li-top{
  border-top: 2px solid white;
}
.nav-container{
}
.nav-li:hover{
  cursor: pointer;
}
.clickable:hover{
  background-color: rgba(255,255,255,.5)
}
.nav-pers,
.nav-prof{
  font-size: 12px;
  font-weight: normal;
  padding: 3px;
  display: none;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.nav-pers-container:hover .nav-pers{
  display:block;
}
.nav-prof-container:hover .nav-prof{
  display: block;
}
.content-block-container{
   padding: 20px;
  margin-top: 10vh;
  height: 40vh;
  margin-bottom: 15vh;
  display: inline-block;
  width: 50%;
  margin-left: auto;
  position: relative;
}
.content-container{ 
  visibility: visible;  
  position: relative;
  
}


.show,
.show1,
.show2,
.show3{
  visibility: visible;  
  -webkit-animation-fill-mode: forwards;  
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s; 
}
.show{
  -webkit-animation-name: appear;
          animation-name: appear;
}
.show1{
  -webkit-animation-name: appear1;
          animation-name: appear1;
}
.show2{
  -webkit-animation-name: appear2;
          animation-name: appear2;
}
.show3{
  -webkit-animation-name: appear3;
          animation-name: appear3;
}
.hide{
  visibility: visible;
  -webkit-animation-name: disappear;
          animation-name: disappear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s; 
}
.hidden{
  visibility: hidden;
}
